import React from 'react';
import s from './AboutProject.module.css';

const AboutProject = () => {
  return (
    <div className={s.content}>
      <h3 className={s.h3}>О нашем проекте</h3>
      <p className={s.bold}>ГРАНТ ПРЕЗИДЕНТА РОССИЙСКОЙ ФЕДЕРАЦИИ НА РАЗВИТИЕ ГРАЖДАНСКОГО ОБЩЕСТВА № 17-1-001972</p>
      <p className={s.bold}>О ПРОЕКТЕ: <span>«Укрепление и развитие взаимодействия общественных, национально-культурных организаций с муниципальными органами власти в решении задач гармонизации межнациональных отношений.</span></p>
      <p>Саратовская область является приграничным, стратегически важным для России регионом, на территории которого проживают представители 136 национальностей. Обстановка в сфере межнациональных отношений в области в целом стабильна. Однако опыт последних десятилетий показывает, что в ряде муниципальных образований все еще велика опасность проявлений межэтнической нетерпимости, вспышек ксенофобии. Это связано с размыванием традиционных нравственных ценностей народов России, правовым нигилизмом, распространенностью негативных стереотипов в отношении некоторых народов, недостаточностью образовательных и культурно-просветительских мер по формированию российской гражданской идентичности. В результате в прошлом десятилетии в некоторых районах бытовые конфликты в молодежной среде переросли в межнациональное противостояние, которое привело к существенным социальным и нравственным потерям. Недостаточно изучен феномен необычайно быстрого изменения межэтнического баланса в ряде сельских поселениях Саратовской области. В некоторых муниципальных образованиях в результате интенсивной миграции происходит замещение коренного населения на представителей этносов, активно переселяющихся из стран СНГ и республик Северного Кавказа. Проведенные в прошлые десятилетия переписи населения не в полной мере осветили картину расселения внутренних и внешних мигрантов (из стран СНГ) по территории Саратовской области. Нет возможности достоверно отследить изменения межэтнического баланса в ряде муниципальных образований. Недостаточно научных исследований, способных составить достоверный прогноз развития народонаселения и возможных вариантов изменения межэтнического баланса в ряде муниципальных образований. Между тем оперативное проведение такого рода исследований позволит разработать и апробировать системные рекомендации по преодолению межэтнической нетерпимости, правового нигилизма, воспитанию культуры межнационального общения, направленные на формирование российской гражданской идентичности. Существенным фактором является ориентация на подготовку сотрудников муниципальных органов власти не владеющих в полной мере знаниями о специфике работы с национальными меньшинствами, о путях интеграции различных этнических групп мигрантов в жизнь местных сообществ, о способах взаимодействия с лидерами национально-культурных объединений, органами культуры, образования. На решение этих проблем направлен наш проект.</p>
      <p className={s.bold}><span>ЦЕЛЬ ПРОЕКТА</span></p>
      <p>Всестороннее изучение работы по гармонизации межнациональных отношений в муниципальных районах Саратовской области в свете Стратегии государственной национальной политики РФ на период до 2025 года и составление специального доклада по этой важнейшей теме. Разработка и апробация системных рекомендаций по преодолению межэтнической нетерпимости, правового нигилизма, воспитанию культуры межнационального общения, направленные на формирование российской гражданской идентичности в муниципальных образованиях Саратовской области.</p>
      <p className={s.bold}><span>Задачи проекта:</span></p>
      <p className={s.bold}><span>Задача №1:</span>Проведение исследований по ряду качественных и количественных показателей, отражающих состояние межнациональных отношений в двенадцати муниципальных образованиях Саратовской области и создание информационной инфраструктуры проектных работ;</p>
      <p className={s.bold}><span>Задача №2:</span>Разработка и апробация системных рекомендаций по решению выявленных проблем в развитии межнациональных, межрелигиозных отношений в муниципальных образованиях;</p>
      <p className={s.bold}><span>Задача №3:</span>Обеспечение работы общественной приемной в Саратове, проведение консультативных встреч и обучающих семинаров для лидеров и активистов национально-культурных объединений, сотрудников местных администраций и правоохранительных органов в муниципальных районах региона;</p>
      <p className={s.bold}><span>Задача №4:</span> Обеспечение и долгосрочное развитие информационной инфраструктуры всех работ по проекту (интерактивный веб-сайт, бесплатные юридические консультации, выпуск информационных бюллетеней, создание и презентация публицистических, обучающих фильмов и т.п.).</p>
      <p></p>
      <p>В процессе проектной деятельности будет изучена работа в муниципальных районах Саратовской области по гармонизации межнациональных отношений в свете Стратегии государственной национальной политики РФ на период до 2025 года. В процессе этой работы будет проведена экспертная оценка межнациональных отношений в двенадцати муниципальных районах, городах Саратове, Энгельсе. Особое внимание будет уделено оценке количественных и качественных показателей, таких как: количество представителей национальных меньшинств, прибывших в места проживания в течение последних 25 лет, решение проблем в сфере занятости, культурных обменов, предотвращение конфликтов в молодежной среде и т.п. Отметим, что в нескольких сельских поселениях некоторые этнические группы составили большинство населения, продолжаются процессы замещения коренного населения прибывающими мигрантами из стран СНГ, республик Северного Кавказа. Эти процессы несут в себе позитивное начало. Однако, одновременно требуют тщательного изучения и разработки дополнительных мер по гармонизации межнациональных отношений и предотвращению конфликтов на этнической почве. Всестороннее изучение названных выше процессов позволит разработать и апробировать определенную систему работы в муниципальных районах, основанную на объединении усилий муниципальных органов власти, общественных, национально-культурных объединений, органов образования и правоохранительных структур. Планируется проведение ряда семинаров-тренингов обучающего характера, на которых в различных формах будут рассмотрены сценарии развития межнациональных отношений. Планируется разработать комплексные рекомендации для муниципальных образований по гармонизации межнациональных отношений и предотвращению конфликтов на этнической почве. Особое внимание будет уделено информационно-просветительской поддержке проектных мероприятий. Будет создан интерактивный веб-сайт, освещающий основные проблемы, с которыми сталкиваются муниципальные органы власти, педагоги, общественники в сфере межнациональных отношений и и пути их решения. Будут созданы два публицистических фильма, в которых будут освещены основные направления совместной работы органов власти, педагогов, общественников, правоохранителей по гармонизации межнациональных отношений. Будет разработана и издана тиражом 500 экз. книга "Гармонизация межнациональных отношений в муниципальных образованиях Саратовской области". Издание станет хорошим подспорьем в решении названных выше задач.</p>
      <p className={s.bold}><span>КОМАНДА ПРОЕКТА</span></p>
      <p className={s.bold}>1. <span>Мякшев Анатолий Павлович,</span> доктор исторических наук, профессор Саратовского государственного университета им. Н.Г. Чернышевского;</p>
      <p className={s.bold}>2. <span>Дорошин Иван Александрович,</span> кандидат исторических наук, доцент Саратовского государственного университета им. Н.Г. Чернышевского;</p>
      <p className={s.bold}>3. <span>Семенов Владимир Васильевич,</span> кандидат политических наук, доцент Поволжского института  управления РАНХиГС;</p>
      <p className={s.bold}>4. <span>Амбарян Амбарцум Владимирович,</span> кандидат исторических наук, доцент  Поволжского института  управления РАНХиГС;</p>
      <p className={s.bold}>5. <span>Зуев Александр Пулатович,</span> кандидат педагогических наук, ответственный секретарь Поволжского отделения Изборского клуба, член Общественной палаты г. Саратова, член Союза писателей России.</p>
    </div>
  );
}

export default AboutProject;